import React from 'react';
import { useTranslation } from 'react-i18next';
import { NavLink } from 'react-router-dom';
import c from "./Help.module.css"

const Help = () => {
    const {t}= useTranslation()
    return(
        <div className={c.wrapper}>
            <NavLink to="/helpForm" className={c.item}>{t("Помочь деятельности Комитета по сохранению наследия Рерихов!")}</NavLink>
        </div>
    )
}

export default Help