import React from "react";
import c from "./News.module.css";
import test_photo from "../../images/news_test_photo.jpg";
import { NavLink } from "react-router-dom";

const News = (props) => {
  let pageCount = Math.ceil(props.totalNews/props.newsPerPage)
  let pages =[]
  let i = 1
  for(i; i<= pageCount; i++){
    pages.push(i)
  }

  let NewElement = React.createRef()
  let onInputChange = () => {
    props.SearchNews(NewElement.current.value.toLowerCase())
    props.CurrentNews()
    props.TotalNews()
  }

  return (
    <div className={c.wrapper}>
      <input className={c.input} placeholder="Поиск..." ref={NewElement} onChange={onInputChange}></input>
      {props.currentNews.slice(0,10).map((e) => {
        return (
          <div className={c.content}>
            <div className={c.img}>
              <img src={test_photo}></img>
            </div>
            <div className={c.textContent}>
              <div className={c.info}>
                <div className={c.date}>{e.date}</div>
                <NavLink to='/new' className={c.name}>
                  {e.name}
                </NavLink>
              </div>
              <div className={c.discription}>
                {e.discription}
              </div>
            </div>
          </div>
        );
      })}
      <div className={c.pagesNumbers}>
      <span className={c.page} onClick={ () => {props.Pagination(1)} }>Начало</span>
      {pages.map( e => {
        return(
        <span className={c.page} onClick={ () => {props.Pagination(e)} }>{e}</span>
        )
      })}
      <span className={c.page} onClick={ () => {props.Pagination(pages[i-2])} }>Конец</span>
      </div>
    </div>
  );
};

export default News;
