import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faBookmark} from "@fortawesome/free-regular-svg-icons"
import React from 'react';
import { NavLink } from 'react-router-dom';
import c from "./Links.module.css"
import { useTranslation } from 'react-i18next';


const Linkss = () => {
    const {t} = useTranslation()
    return(
        <div className={c.wrapper}>
            <div className={c.container}>
                <div className={c.firstLine}>
                    <NavLink to = "/home" className={navData => navData.isActive ? c.active : c.item}>{t("Главная")}</NavLink>
                    <FontAwesomeIcon icon={faBookmark} className={c.icon} />
                    <NavLink to = "/about" className={navData => navData.isActive ? c.active : c.item}>{t("Об организации")}</NavLink>
                    <FontAwesomeIcon icon={faBookmark} className={c.icon}/>
                    <NavLink to = "/afish" className={navData => navData.isActive ? c.active : c.item}>{t("Афиша")}</NavLink>
                    <FontAwesomeIcon icon={faBookmark} className={c.icon} />
                    <NavLink to = "/news" className={navData => navData.isActive ? c.active : c.item}>{t("Новости")}</NavLink>
                    <FontAwesomeIcon icon={faBookmark} className={c.icon} />
                    <NavLink to = "/family" className={navData => navData.isActive ? c.active : c.item}>{t("Семья Рерихов")}</NavLink>
                    <FontAwesomeIcon icon={faBookmark} className={c.icon} />
                    <NavLink to = "/teach" className={navData => navData.isActive ? c.active : c.item}>{t("Учение Живая Этика")}</NavLink>
                    <FontAwesomeIcon icon={faBookmark} className={c.icon} />
                    <NavLink to = "/shaposhnikova" className={navData => navData.isActive ? c.active : c.item}>{t("Л.В.Шапошникова")}</NavLink>
                    <FontAwesomeIcon icon={faBookmark} className={c.icon} />
                    <NavLink to = "/shaposhnikova" className={navData => navData.isActive ? c.active : c.item}>{t("Фильмы")}</NavLink>
                </div>
                <div className={c.secondLine}>
                    <NavLink to = "/konference" className={navData => navData.isActive ? c.active : c.item}>{t("Конференции и чтения")}</NavLink>
                    <FontAwesomeIcon icon={faBookmark} className={c.icon} />
                    <NavLink to = "/culture" className={navData => navData.isActive ? c.active : c.item}>{t("Культурно-просветительская деятельность")}</NavLink>
                    <FontAwesomeIcon icon={faBookmark} className={c.icon} />
                    <NavLink to = "/def" className={navData => navData.isActive ? c.active : c.item}>{t("Защита наследия")}</NavLink>
                    <FontAwesomeIcon icon={faBookmark} className={c.icon} />
                    <NavLink to = "/contacts" className={navData => navData.isActive ? c.active : c.item}>{t("Сотрудничество")}</NavLink>
                    <FontAwesomeIcon icon={faBookmark} className={c.icon} />
                    <NavLink to = "/contacts" className={navData => navData.isActive ? c.active : c.item}>{t("Контакты")}</NavLink>
                </div>  
            </div>
        </div>
    )
}

export default Linkss