  export const newsReduser = (state = initialState, action) => {
      switch(action.type){
        case "PAGINATION":
            return{
                ...state,
                currentNews: state.searchNews.length>0? state.searchNews.slice((action.page * state.newsPerPage)-10, (action.page * state.newsPerPage)) : state.news.slice((action.page * state.newsPerPage)-10, (action.page * state.newsPerPage))
            }
        case "TOTAL_NEWS":
            return{
                ...state,
                totalNews: state.currentNews.length
            }
        case "CURRENT_NEWS":
            return{
                ...state,
                currentNews: action.input!=0? state.searchNews : state.news 
            }
        case "SEARCH_NEWS":
            return{
                ...state,
                searchNews: state.news.filter( n => {
                    return (/*n.discription.toLowerCase().includes(action.input) || */n.name.toLowerCase().includes(action.input))
                  } )
            }
        default: return state
      }
  }
  
  export const PaginationAC = (page) => {
    return{
        type: "PAGINATION",
        page
    }
  }
  export const TotalNewsAC = () => {
    return{
        type: "TOTAL_NEWS",
    }
  }
  
  export const CurrentNewsAC = () => {
    return{
        type: "CURRENT_NEWS",
    }
  }
  export const SearchNewsAC = (input) => {
    return{
        type: "SEARCH_NEWS",
        input
    }
  }
  
  