import React from "react";
import Slider from "../../Slider/Slider";
import c from "./New.module.css";


const New = (props) => {
  
  return (
    <div className={c.wrapper}>
      <div className={c.date}>31.10.2022</div>
      <div className={c.headerText}>Завершается маршрут передвижной выставки «Мы – дети Космоса» по Смоленской области</div>
      <div className={c.img}><img src="https://pushkinmuseum.art/museum/22_img_pc.jpg"></img></div>
      <div className={c.contentText}>Выставка «Мы – дети Космоса» завершает свое путешествие по городам Смоленской области. Это одна из семи передвижных выставок Международного конкурса детского рисунка «Мы – дети Космоса», организованных Благотворительным Фондом имени Елены Ивановны Рерих в различных регионах России и за рубежом.

Свой путь по Смоленщине экспозиция начала в январе 2022 года с города Сафонова. Затем продолжила маршрут в городах Гагарин, Вязьма, Ярцево, Дорогобуж, Десногорск и в октябре открылась в поселке Шумячи. За девять месяцев более 3000 зрителей познакомились с уникальным творчеством участников конкурса, которое открывает людям духовные просторы Космоса, его невыразимую Красоту, Мудрость, краски и звуки.

Это было необыкновенное путешествие. Мы приобрели на Смоленской земле много новых доброжелательных и гостеприимных друзей, провели незабываемые встречи с детьми и педагогами, получили массу новых впечатлений. Творческая молодежь проявила к философскому содержанию экспозиции неподдельный интерес. Одним из результатов выставки стало участие в конкурсе «Мы – дети Космоса» 2022 года учеников Сафоновской детской художественной школы имени В.М.Кирилова, а также детских художественных школ городов Гагарина и Вязьмы. В конкурсе 2023 года уже готовятся принять участие учащиеся Десногорской детской художественной школы.

В поселке Шумячи выставка открылась в художественно-краеведческом музее. Здесь она будет экспонироваться до 6 декабря 2022 года.

Шумячи располагается на территории древнейших поселений, на исторической Мстиславщине. Упоминание о культуре этих народов уходит в глубь веков (I в. до н.э.). В Шумячском районе находится около пятисот курганов Х–ХIII веков. Однако в летописях название Шумячи впервые появляется только с 1587 года. До нашего времени здесь сохранился костел Святого Михаила, памятник архитектуры XVII в., который в конце XIX века был перестроен в православную церковь.

На этой земле родился один из известнейших в мире писателей-фантастов Айзек Азимов. В детстве его родители мигрировали в Америку, но память о своей малой родине Айзек навсегда сохранил в сердце. В Шумячском художественно-краеведческий музее хранится следующая запись писателя:

«Очень приятно получить весточку из города, где я родился. Мои родители были счастливы там… После того как они приехали в США, они всегда в нежностью вспоминали родину. Сам я этого не помню, так как мне не было и трех лет, но я никогда не забывал, где родился».

В апреле в память об Айзеке Азимове в Шумячи ежегодно проводятся Азимовские чтения.

В этих краях также родился и другой знаменитый россиянин – Семен Алексеевич Лавочкин. Легендарный российский авиаконструктор – уроженец местечка Петровичи Шумячского района.

Международный конкурс детского рисунка «Мы – дети Космоса» – это, прежде всего, конкурс о Красоте: Красоте Мироздания, Красоте человеческой души, Красоте как высокой творческой энергии, преображающей и возвышающей духовный мир человека. Работы юных художников не только прекрасны, но и наполнены глубоким философским смыслом. Они заставляют задуматься о законах Вселенной, о единстве и взаимосвязи всего сущего, о месте человека в мире и его ответственности за все происходящее. Каждое мгновение человек делает свой выбор, направленный в сторону добра или зла, и именно на нем лежит ответственность за происходящее в мире.

В одном из своих интервью Айзек Азимов отметил, что «в людей нужно верить, верить в то, что человек хороший и способен делать хорошие поступки. Думая о людях хорошо, повышается ощущение счастья, даже если имеют место быть случайные разочарования, они не способны осквернить радость веры. Если же на человечество смотреть цинично, то результат будет противоположным, и способность наслаждаться хорошими вещами со временем исчезнет».

Конкурс «Мы – дети Космоса» собирает юных художников в возрасте от 5 до 14 лет из разных стран мира и уже шестой год подряд проводится Благотворительным Фондом имени Е.И.Рерих совместно с Международным Центром Рерихов при поддержке Международного Союза педагогов-художников, Российской академии художеств, Российской академии естественных наук и Ассоциации музеев космонавтики России.</div>
      <div className={c.slider}><Slider /></div>
      <div className={c.up}><a href="#">К началу страницы ^</a></div>
    </div>
  );
};

export default New;
