import React from 'react';
import c from "./ChangePhotoLink.module.css"
import savemuseum from "../../../gif/gif5.gif"
import virtmuseum from "../../../gif/virt-museum.gif"
import pact from "../../../images/slide8.jpg"
import kosmo from "../../../gif/gif4.gif"
import konference from "../../../images/slide12.jpg"

const srcs = [
    {src: virtmuseum},
    {src: savemuseum},
    {src: pact},
    {src: kosmo},
    {src: konference},

]

const ChangePhotoLink = () => {
    return(
        <div className={c.wrapper}>
                    {srcs.map( e => {
                        return(
                            <img className={c.item} src={e.src}></img>
                        )
                    } )}
                    <div className={c.test}></div>
        </div>
    )
}

export default ChangePhotoLink