import React from 'react';
import ChangePhotoLink from './ChangePhotoLink/ChangePhotoLink';
import c from "./ChangePhotoLinks.module.css"
import { useTranslation } from 'react-i18next';

const ChangePhotoLinks = () => {

    const {t, i18n} = useTranslation()
    const changeLanguage = (language) => {
        i18n.changeLanguage(language)
    }

    return(
        <div className={c.wrapper}>
            <div className={c.content}>
                <div className={c.lang}>
                    <button onClick={()=>{changeLanguage("ru")}}>rus</button>
                    <button onClick={()=>{changeLanguage("bel")}}>bel</button>
                    <button onClick={()=>{changeLanguage("en")}}>eng</button>
                </div>
                <ChangePhotoLink />
            </div>
        </div>
    )
}

export default ChangePhotoLinks