import React from "react";
import { connect } from "react-redux";
import News from "../Components/News/News";
import { CurrentNewsAC, PaginationAC, SearchNewsAC, TotalNewsAC } from "../DATA/news-reduser";

const mapStateToProps = (state) => {
  return {
    searchNews: state.News.searchNews,
    currentNews: state.News.currentNews,
    new: state.News.news,
    totalNews: state.News.totalNews,
    newsPerPage: state.News.newsPerPage,
    totalPages: state.News.totalPages
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    Pagination: (page) => {
      dispatch(PaginationAC(page))
    },
    TotalNews: () => {
      dispatch(TotalNewsAC())
    },
    CurrentNews: () => {
      dispatch(CurrentNewsAC())
    },
    SearchNews: (input) => {
      dispatch(SearchNewsAC(input))
    }
  };
};

class newsContainer extends React.Component {

  componentDidMount() {
    this.props.CurrentNews()
    this.props.TotalNews()
    this.props.SearchNews("")
  }

  render() {
    return(
      <News TotalNews = {this.props.TotalNews} CurrentNews = {this.props.CurrentNews} new = {this.props.new} totalNews = {this.props.totalNews} newsPerPage = {this.props.newsPerPage} totalPages = {this.props.totalPages}
      Pagination = {this.props.Pagination} currentNews= {this.props.currentNews} SearchNews = {this.props.SearchNews} searchNews = {this.props.searchNews}/>
    )
  }
}


const NewsContainer = connect(mapStateToProps, mapDispatchToProps)(newsContainer);

export default NewsContainer
