import React from "react";
import HeaderLogo from "../../images/logo-copy2.png";
import backRight from "../../images/мцр3.png";
import backLeft from "../../images/rerich-gimalai.jpg";
import backmain from "../../images/backkk.jpg";
import c from "./HeaderMainPage.module.css";
import { useTranslation } from "react-i18next";

const HeaderMainPage = () => {
    const {t} = useTranslation()
  return (
    <div className={c.mainWrapper}>
    <div className={c.wrapper}>
      <div className={c.logoContent}>
        <img src={HeaderLogo} alt="Logo"></img>
        <div className={c.logoTextContainer}>
          <div className={c.logoText}>
            {t("Отделение Международного Комитета")}
            </div>
          <div className={c.logoText}>
            {t("по сохранению наследия Рерихов.")} <br></br>{t("Республика Беларусь")}
          </div>
        </div>
      </div>
      <div className={c.discription}>
        {t("главноеописание")}
      </div>
      {/* <img src={backmain} className={c.backmain}></img> */}
      {/* <img alt='' className={c.backRight} src={backRight}></img> */}
      {/* <img alt='' className={c.backLeft} src={backLeft}></img> */}
    </div>
    {/* <div className={c.logoStatui}><img src={backRight}></img></div> */}
    </div>
  );
};

export default HeaderMainPage;
